import { defineComponent } from "vue";
export default defineComponent({
  props: {
    title: {
      type: String,
      default: ""
    },
    listData: {
      type: Array,
      require: true
    },
    propList: {
      type: Array,
      require: true
    },
    showIndexColumn: {
      type: Boolean,
      default: false
    },
    showSelectColumn: {
      type: Boolean,
      default: false
    },
    listCount: {
      type: Number,
      default: 0
    },
    page: {
      type: Object,
      default: () => ({
        currentPage: 1,
        pageSize: 10
      })
    },
    childrenProps: {
      type: Object,
      default: () => ({
        treeProps: {
          children: "children",
          hasChildren: true
        }
      })
    },
    showFooter: {
      type: Boolean,
      default: true
    }
  },
  emits: ["update:page", "selectionChange"],

  setup(props, {
    emit
  }) {
    const handleCurrentChange = currentPage => {
      emit("update:page", { ...props.page,
        currentPage
      });
    };

    const handleSizeChange = pageSize => {
      emit("update:page", { ...props.page,
        pageSize
      });
    };

    const handleSelectionChange = value => {
      emit("selectionChange", value);
    };

    return {
      handleCurrentChange,
      handleSizeChange,
      handleSelectionChange
    };
  }

});